.signup-form {
  max-width: 400px;  /* center + limit width */
  margin: 0 auto;
  padding: 20px;
}

.signup-form h2 {
  text-align: center;
  margin-bottom: 20px;
}

.signup-form form {
  display: flex;
  flex-direction: column; /* stack inputs vertically */
  gap: 12px; /* spacing between fields */
}

.signup-form input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.signup-form button {
  padding: 12px;
  background-color: #007bff;
  border: none;
  color: white;
  font-size: 16px;
  border-radius: 6px;
  cursor: pointer;
}

.signup-form button:hover {
  background-color: #0056b3;
}


.form-wrap {
    max-width: 420px;
    margin: 32px auto;
    background: #fff;
    border: 1px solid #e5e7eb;
    border-radius: 8px;
    padding: 24px 28px;
  }
  
  .form-wrap h1 { margin: 0 0 16px; font-size: 24px; color: #111827; }
  
  .form {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  label {
    font-size: 14px;
    font-weight: 600;
    color: #374151;
  }
  
  input, select {
    height: 44px;
    padding: 10px 12px;
    border: 1px solid #d1d5db;
    border-radius: 6px;
    font-size: 14px;
  }
  
  input:focus, select:focus {
    border-color: #2563eb;
    outline: none;
    box-shadow: 0 0 0 2px rgba(37,99,235,.25);
  }
  
  .error {
    color: #b91c1c;
    font-size: 12px;
    margin: 2px 0 8px;
  }
  
  .actions {
    display: flex;
    gap: 12px;
    margin-top: 12px;
  }
  
  button {
    padding: 10px 16px;
    border: none;
    border-radius: 6px;
    font-weight: 700;
    font-size: 14px;
    cursor: pointer;
    background: #2563eb;
    color: #fff;
  }
  
  button:hover { background: #1d4ed8; }
  button.secondary { background: #f3f4f6; color: #111827; }
  button.secondary:hover { background: #e5e7eb; }
  